*{
    margin: 0;
    padding: 0;
}
html,
body{
    height: 100%;
    font-family: 'Reaver',sans-serif;
    color:#ffffffcc;
    line-height: 150%;
    font-size: 21px;
    letter-spacing: 0.5px;
    font-weight: 100;
    line-height: 33px;
    overflow-x: hidden;
}
@font-face{
    font-family: 'Reaver',sans-serif;
    src: url(../fonts/reaver-regular.otf);
}
@font-face{
    font-family: 'Radiance';
    src: url(../fonts/radiance-bold.otf);
}
.wrapper{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.title{
    display: block;
    text-align: center;
    font-family: 'Reaver',serif;
    color:#fff;
    font-weight: 700;
    line-height: 47px;
    font-size: 43px;
    text-shadow: 0px 0px 10px #000, 0px 0px 5px #000;
    margin: 0 auto;
}
.bold{
    display: inline;
    font-family: 'Radiance';
    color: #fff;
}
.wrapper{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.link{
    font-family: 'Radiance';
    color: rgb(204, 204, 204);
    text-decoration: underline; 
    transition: 0.3s;
}
.link:hover{
    color: #fff;
}
.gold{
    font-size: 19px;
    display: inline;
    color:#ddc068;
    font-weight: 600;
}
.gold::after{
    content: '';
    display: inline-block;
    margin-left:3px;
    width: 18px;
    height: 18px;
    background-image: url(/src/images/Gold.png);
    background-size: cover;
    background-position: center;
}