.navbar{
    width: 100vw;
    position: absolute;
    box-sizing: border-box;
    background-color: #1c1c1cf7;
}
.container{
    max-width: 1500px;
    box-sizing: border-box;
    padding: 15px;
    margin: 0 auto;
}
.navbar__inner{
    display: flex;
    align-items: center;
    justify-content: start;
}
.link{
    display: flex;
    font-family: 'Reaver',sans-serif;
    color:#dbdbdbcc;
    margin: 0 10px;
    line-height: 150%;
    font-size: 22px;
    letter-spacing: 0.5px;
    font-weight: 100;
    line-height: 33px;
    text-decoration: none;
    transition: 0.4s;
}
.link:hover{
    color: #fff;
}
.logo{
    display: flex;
    height: 40px;
    transition: 0.4s;
    filter:brightness(0);
    filter:contrast(0);
}
.logo:hover{
    transform: scale(1.08);
    filter:brightness(1);
    filter:contrast(1);
}